.Banner {
  height: 400px;
  position: relative;

}
.Banner .Media {
  background-color: white;
  height: 100%;
  overflow: hidden;
  position: relative;
  transition: 300ms;
  cursor: pointer;
}

.Banner .Media:hover {
  filter: brightness(115%);
}
.Banner .BannerGrid {
  height: 95%;
  position: relative;
}
.Banner .Content {
  color: white;
  background-color: red;
  height: 100%;
  position: relative;
  cursor: pointer;
  padding: 30px;
  transition: 300ms;
}
.Banner .Content:hover,
.Banner .Content:active {
  background-color: tomato;
}
.Banner .Content:hover .ViewButton,
.Banner .Content:active .ViewButton {
  background-color: white;
  color: red;
}
.Banner .Content .Title {
  font-size: 42px;
  font-weight: 500;
}
.Banner .Content .Caption {
  margin-top: 10px;
  font-size: 24px;
}
.Banner .Content .ViewButton {
  margin-top: 40px;
  color: white;
  font-size: 24px;
  border: 3px solid white;
  text-transform: capitalize;
  transition: 200ms;
}
